import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ArticleService} from "../../providers/article.service";
import {Geolocation} from '@ionic-native/geolocation';

@IonicPage()
@Component({
  selector: 'page-store-service',
  templateUrl: 'storeService.html',
})
export class StoreServicePage {

  type: string = "services";
  services = [];
  services_backup = [];
  position: any = {latitude : 0, longitude: 0};

  subscribeService;
  alive: boolean = true;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private articleService: ArticleService,
              public geolocation: Geolocation) {

  }

  ionViewDidLoad() {
    //this.services = this.articleService.getServices();
    this.subscribeService = this.articleService.getServices().takeWhile(() => this.alive).subscribe(articles => {
      articles.forEach(article => {{
        this.services.push(article);
        this.services_backup.push(article);
      }
      })
    });
    this.geolocation.getCurrentPosition({ maximumAge: 3000, timeout: 5000, enableHighAccuracy: true })
      .then((position) => {
        this.position.latitude = position.coords.latitude;
        this.position.longitude = position.coords.longitude;
      }).catch(err => {
      this.position.latitude = "n/a";
      this.position.longitude = "n/a";
    });
  }

  ionViewWillLeave() {
    this.alive = false;
  }

  seeDetails(item) {
    this.navCtrl.push('ArticlePage',
      {article: item, key: item.$key});
  }

  seeDetailsService(item) {
    //console.log(item);
    this.navCtrl.push('ServicePage',
      {service: item, key: item.$key});
  }

  calculateDistance(value) {
    if (value) {
      const positions = value.split(':');
      const me = positions[0].split(',');
      const other = positions[1].split(',');

      return getDistanceFromLatLonInKm(me[0], me[1], other[0], other[1]).toFixed(1);
    }
  }

  getItems(ev) {
    this.services = this.services_backup;
    // set val to the value of the ev target
    var val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val) {
      let text = val.toLowerCase();
      this.services = this.services
        .filter((article) => {
          return article.title.toLowerCase().indexOf(text) >= 0
            || article.type.toLowerCase().indexOf(text) >= 0
            || article.description.toLowerCase().indexOf(text) >= 0;
        });
    }
  }
}

function getDistanceFromLatLonInKm(lat1,lon1,lat2,lon2) {
  var R = 6371; // Radius of the earth in km
  var dLat = deg2rad(lat2-lat1);  // deg2rad below
  var dLon = deg2rad(lon2-lon1);
  var a =
    Math.sin(dLat/2) * Math.sin(dLat/2) +
    Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
    Math.sin(dLon/2) * Math.sin(dLon/2)
  ;
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
  var d = R * c; // Distance in km
  return d;
}

function deg2rad(deg) {
  return deg * (Math.PI/180)
}




